import { Component, OnInit } from '@angular/core';
import * as dd from '../../data-source/times';
import { PrayerSchedule } from '../../interfaces/prayer-schedule.interface';
import { FrontendData } from '../../interfaces/frontend-data.interface';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit {
  prayersNames: string[] = ["fa", "du", "th", "as", "mo", "ish"];
  // catch the changeing time
  changeToSommerTime: boolean = false;
  changeToWinterTime: boolean = false;
  data: any = dd.default;

  FEData: FrontendData[];
  todaysPrayersSchedule: PrayerSchedule;
  currentMonth: string;
  _currentTimeStamp: number;
  datum: string;
  currentPrayPeriod: string;

  refreshRate = 30 * 1000;
  remainingTimeNum: number;
  remainingTimeStr: string;
  currentDate: Date; // this is a global value for the whole life cycle of this app.
  periodName: string;

  changePeriodToSummer: boolean;
  changePeriodToWinter: boolean;

  // Built the following apk(s): 
  // /Users/aref / ionic / PrayerTimesInBerlin / platforms / android / app / build / outputs / apk / debug / app - debug.apk
  ngOnInit() {
    // this.currentDate = new Date('2019-10-28T05:10:00'); // this is a global value for the whole life cycle of this app.
    this.currentDate = new Date();
    console.log("ngOnInit()...");
    let month = this.currentDate.getMonth() + 1;
    this.currentMonth = this.getMonthName(month);

    let day = this.currentDate.getDate();
    let year = this.currentDate.getFullYear()

    // setting the currentTimeStamp once in the app life-cylce
    let hour = this.currentDate.getHours();
    let minutes = this.currentDate.getMinutes();
    this._currentTimeStamp = (hour * 60) + minutes;
    // getting prayer times for this day
    let dailySchedule = this.data[this.currentMonth][day];
    this.isChangingTime(month, day)
    if (this.changePeriodToSummer) {
      this.todaysPrayersSchedule = this.adjustTimeForChangingPeriods(dailySchedule);
      console.log("changing time = " + this.changePeriodToSummer);
    } else if (this.changePeriodToWinter) {
      this.todaysPrayersSchedule = this.adjustTimeForChangingPeriods(dailySchedule);
      console.log("changing time = " + this.changePeriodToWinter);
    } else {
      this.todaysPrayersSchedule = dailySchedule;
    }
    this.FEData = this.createFrontEndData(this.todaysPrayersSchedule);
    this.datum = day + " " + this.currentMonth + " " + year;
    this.findCurrentPrayPeriod(this.todaysPrayersSchedule)

    setTimeout(() => {
      this.ngOnInit()
    }, this.refreshRate);
  }

  createFrontEndData(schedule: PrayerSchedule): FrontendData[] {
    let lst: FrontendData[] = [];

    let ll = ['fa', 'du', 'th', 'as', 'mo', 'ish'];
    for (let x of ll) {
      let obj: FrontendData = { arabicName: "", germanName: "", time: "" };
      switch (x) {
        case 'fa':
          obj.arabicName = "الفجر";
          obj.germanName = "Frühlicht";
          obj.time = schedule.fa;
          lst.push(obj);
          break;
        case 'du':
          obj.arabicName = "الشروق";
          obj.germanName = "S'Aufgang";
          obj.time = schedule.du;
          lst.push(obj);
          break;
        case 'th':
          obj.arabicName = "الظهر";
          obj.germanName = "Mittag";
          obj.time = schedule.th;
          lst.push(obj);
          break;
        case 'as':
          obj.arabicName = "العصر";
          obj.germanName = "Nachmittag";
          obj.time = schedule.as;
          lst.push(obj);
          break;
        case 'mo':
          obj.arabicName = "المغرب";
          obj.germanName = "Abend";
          obj.time = schedule.mo;
          lst.push(obj);
          break;
        case 'ish':
          obj.arabicName = "العشاء";
          obj.germanName = "Nacht";
          obj.time = schedule.ish;
          lst.push(obj);
          break;
        // default:
        //   obj.arabicName = "التهجد";
        //   obj.germanName = "Al-Tahadgud";
        //   obj.time = "";
        //   lst.push(obj);
        //   break;
      }
    }
    return lst;
  }

  getMonthName(n: number): string {
    let monthDictionary = {
      1: "Januar",
      2: "Februar",
      3: "März",
      4: "April",
      5: "Mai",
      6: "Juni",
      7: "Juli",
      8: "August",
      9: "September",
      10: "Oktober",
      11: "November",
      12: "Dezember"
    }
    return monthDictionary[n];
  }

  get currentTimeStamp(): number {
    return this._currentTimeStamp;
  }
  /**
   * find which pray period we are in right now
   * @param schedule todays prayer schedule
   */
  findCurrentPrayPeriod(schedule: any) {
    let lst = [];
    for (let i = 0; i < this.prayersNames.length; i++) {
      let prayerName = this.prayersNames[i];
      let prayerTimeStamp = this.calculatePrayerTime(schedule[prayerName]);
      if (prayerTimeStamp <= this.currentTimeStamp) {
        lst.push(prayerName)
      }
    }
    if (lst.length > 0) {
      this.periodName = this.mappingPrayerName(lst[lst.length - 1]);
    } else {
      this.periodName = "Al-Tahadgud";
    }
    this.calculateRemainingTime();
  }

  calculatePrayerTime(time: string): number {
    let tt = time.split(":");
    let hour = parseInt(tt[0]) * 60;
    let minutes = parseInt(tt[1]);
    return hour + minutes;
  }

  mappingPrayerName(name: string): string {
    let _name: string;
    switch (name) {
      case "fa":
        _name = "Al-Fajer";
        break;
      case "du":
        _name = "Al-Duha";
        break;
      case "th":
        _name = "Al-Thuher";
        break;
      case "as":
        _name = "Al-Asser";
        break;
      case "mo":
        _name = "Al-Moghreb";
        break;
      case "ish":
        _name = "Al-Ischaa";
        break;
      default:
        console.error("Provided shortcuts are only fa, du, th, as, mo and ish");
    }
    return _name;
  }

  remappingPrayerName(name: string): string {
    let _name: string;
    switch (name) {
      case "Al-Fajer":
        _name = "fa";
        break;
      case "Al-Duha":
        _name = "du";
        break;
      case "Al-Thuher":
        _name = "th";
        break;
      case "Al-Asser":
        _name = "as";
        break;
      case "Al-Moghreb":
        _name = "mo";
        break;
      case "Al-Ischaa":
        _name = "ish";
        break;
      case "Al-Tahadgud":
        _name = "ta";
        break;
      default:
        console.error("Provided shortcuts are only Al-fajer, Al-Duha, Al-Thuher, Al-Asser, Al-Moghreb and Al-Ischaa");
    }
    return _name;
  }
  formateTime(num: number) {
    let h: number = Math.floor(num / 60);
    let m: number = num % 60;
    let hour: string = "";
    let minute: string = "";
    hour = h < 10 ? "0" + h.toString() : h.toString();
    minute = m < 10 ? "0" + m.toString() : m.toString();
    return hour + ":" + minute;
  }

  calculateRemainingTime() {
    let current = this.remappingPrayerName(this.periodName);
    switch (current) {
      case "ish":
        this.remainingTimeNum = (23 * 60) + 59 - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum)
        break;
      case "ta":
        let fajerTime = this.calculatePrayerTime(this.todaysPrayersSchedule["fa"]);
        this.remainingTimeNum = fajerTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
      case "fa":
        let duhaTime = this.calculatePrayerTime(this.todaysPrayersSchedule["du"]);
        this.remainingTimeNum = duhaTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
      case "du":
        let thuherTime = this.calculatePrayerTime(this.todaysPrayersSchedule["th"]);
        this.remainingTimeNum = thuherTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
      case "th":
        let asserTime = this.calculatePrayerTime(this.todaysPrayersSchedule["as"]);
        this.remainingTimeNum = asserTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
      case "as":
        let moghrebTime = this.calculatePrayerTime(this.todaysPrayersSchedule["mo"]);
        this.remainingTimeNum = moghrebTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
      case "mo":
        let ischaaTime = this.calculatePrayerTime(this.todaysPrayersSchedule["ish"]);
        this.remainingTimeNum = ischaaTime - this.currentTimeStamp;
        this.remainingTimeStr = this.formateTime(this.remainingTimeNum);
        break;
    }
  }

  adjustTimeForChangingPeriods(obj: PrayerSchedule): any {
    let o = {};
    for (let x of this.prayersNames) {
      let time = this.calculatePrayerTime(obj[x]);
      if (this.changePeriodToSummer) {
        // summer-time add 1 hour = 60 minutes
        o[x] = this.formateTime(time + 60);
      } else if (this.changePeriodToWinter) {
        // summer-time substract 1 hour = 60 minutes
        o[x] = this.formateTime(time - 60);
      }
    }
    return o;
  }

  isChangingTime(month, day) {

    // Sommer
    if (month == 3 && day >= 25) {
      this.WinterSummerChange("summer", day);
    } else if (month == 10 && day >= 25) {
      this.WinterSummerChange("winter", day);
    } else {
      this.changePeriodToSummer = false;
      this.changePeriodToWinter = false;
    }
  }
  WinterSummerChange(season: string, day) {
    let daysDict = {
      0: "Su",
      1: "Mo",
      2: "Tu",
      3: "We",
      4: "Th",
      5: "Fr",
      6: "Sa"
    };

    if (season == "summer") {
      //  if we are on Sunday, thus this is the last Sunday in the month
      if (this.currentDate.getDay() == 0) {
        this.changePeriodToSummer = true;
        return this.changePeriodToSummer;
      }
      // if we are on the 25th of March or later, but today is not Sunday,
      // check if we passed the last Sunday in the month
      else if (this.currentDate.getDay() != 0) {

        // it should not be greater than 6 (0-6)
        let diffDayNum = 31 - day;
        let ii = 1; // we know today is not sunday, and we want to check tommorrow!
        let days = [];

        while (diffDayNum > 0) {
          let dayKey = this.currentDate.getDay() + ii;
          // reset ii to not exceed the keys in the dictionary
          if (dayKey > 6) {
            let x = (dayKey % 6) - 1;
            days.push(daysDict[x]);
          } else {
            days.push(daysDict[this.currentDate.getDay() + ii]);
          }

          ii++;
          diffDayNum--;
        }
        // is Su one of the arr member?
        let position = days.indexOf("Su");
        if (position == -1) { // no Sunday was found in the days-array
          this.changePeriodToSummer = true;
          return this.changePeriodToSummer;
        } else {
          // the timeseason is not changed yet
          this.changePeriodToSummer = false;
          return this.changePeriodToSummer;
        }

      }
    } else if (season == "winter") {
      //  if we are on Sunday, thus this is the last Sunday in the month
      if (this.currentDate.getDay() == 0) {
        this.changePeriodToWinter = true;
        return this.changePeriodToWinter;
      }
      // if we are on the 25th of March or later, but today is not Sunday,
      // check if we passed the last Sunday in the month
      else if (this.currentDate.getDay() != 0) {

        // it should not be greater than 6 (0-6)
        let diffDayNum = 31 - day;
        let ii = 1; // we know today is not sunday, and we want to check tommorrow!
        let days = [];

        while (diffDayNum > 0) {
          let dayKey = this.currentDate.getDay() + ii;
          // reset ii to not exceed the keys in the dictionary
          if (dayKey > 6) {
            let x = (dayKey % 6) - 1;
            days.push(daysDict[x]);
          } else {
            days.push(daysDict[this.currentDate.getDay() + ii]);
          }

          ii++;
          diffDayNum--;
        }
        // is Su one of the arr member?
        let position = days.indexOf("Su");
        if (position == -1) { // no Sunday was found in the days-array
          this.changePeriodToWinter = true;
          return this.changePeriodToWinter;
        } else {
          // the timeseason is not changed yet
          this.changePeriodToWinter = false;
          return this.changePeriodToWinter;
        }
      }
    }
  }

}




